import { SwiperSlide } from "swiper/react"
import { CuratedSuggestedPrompt } from "@rp/react/common/hooks"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { SwiperWithAbsoluteArrows } from "../Swiper"
import { SuggestedPromptCard } from "./SuggestedPromptCard"

type Props = {
  onPromptClick?: (prompt: CuratedSuggestedPrompt) => void
  prompts: Array<CuratedSuggestedPrompt> | undefined
}

export const SuggestedPrompts = ({ onPromptClick, prompts }: Props) => {
  const { isMobile } = useDeviceInfo()
  const slidesPerView = isMobile ? 1.5 : 4
  const isLoading = !prompts
  const visualized = prompts
    ? prompts
    : [...Array(4).keys()].map((_, index) => {
        const prompt: CuratedSuggestedPrompt = {
          id: `loading-${index}`,
          label: index.toString(),
          icon: {
            id: null,
            label: "",
            color: "",
          },
          prompt: "",
          relatedTopics: [],
          blocked: false,
          entitlements: [],
          watchlist: false,
        }
        return prompt
      })

  return (
    <Box
      sx={{
        position: "relative",
        width: "100%",
        maxWidth: "100%",
        height: isMobile ? "146px" : "200px",
        "& .swiper .swiper-wrapper": {
          height: isMobile ? "146px" : "200px",
        },
        "& .swiper": {
          width: "100%",
        },
        "& .swiper .swiper-slide": {
          minWidth: "0px",
          maxWidth: isMobile ? "calc(100% - 32px)" : undefined,
        },
        "& .swiper .swiper-slide > div": {
          boxShadow: "0px 4px 14px 0px rgba(0, 0, 0, 0.35)",
          marginLeft: isMobile ? "16px" : 0,
          height: isMobile ? "120px" : undefined,
        },

        "& .swiper .swiper-slide:last-of-type > div": {
          marginRight: isMobile ? "16px" : "0px",
        },
        "& .swiper-button-disabled": {
          display: "none",
        },
        "& .arrow-btn.next": {
          right: isMobile ? undefined : "-36px",
        },
        "& .arrow-btn.prev": {
          left: isMobile ? undefined : "-36px",
        },
      }}
    >
      <SwiperWithAbsoluteArrows
        slidesPerView={slidesPerView}
        spaceBetween={isMobile ? 0 : 16}
        slidesPerGroup={isMobile ? 1 : slidesPerView}
        itemsCount={visualized.length}
        disablePrev={!!(prompts && prompts?.length < 5)}
        disableNext={!!(prompts && prompts?.length < 5)}
      >
        {visualized.map(item => {
          return (
            <SwiperSlide key={item.id}>
              <SuggestedPromptCard
                onPromptClick={onPromptClick}
                prompt={item}
                isLoading={isLoading}
              />
            </SwiperSlide>
          )
        })}
      </SwiperWithAbsoluteArrows>
    </Box>
  )
}
